img {
  max-width: 100%;
  height: auto;
}

input {
  color-scheme: dark;
}

::-webkit-scrollbar {
  width: 12px;
  background-color: #2f2c28;
}

::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background: linear-gradient(transparent, #3a9fc9);
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(transparent, rgba(58, 159, 201, 0.6));
}

@font-face {
  font-family: "Rubik";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/rubik-light.woff2") format("woff2"), url("../fonts/rubik-light.woff") format("woff");
}
@font-face {
  font-family: "Rubik";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/rubik-medium.woff2") format("woff2"), url("../fonts/rubik-medium.woff") format("woff");
}
@keyframes loading {
  0% {
    transform: translateX(0) translateY(0);
    background-color: #3a9fc9;
    box-shadow: 0 0 10px #3a9fc9;
  }
  25% {
    transform: translateX(13px) rotate(90deg);
    background-color: #ff4040;
    box-shadow: 0 0 10px #ff4040;
  }
  50% {
    transform: translateX(13px) translateY(-13px) rotate(180deg);
    background-color: #2f2c28;
    box-shadow: 0 0 10px #2f2c28;
  }
  75% {
    transform: translateX(0) translateY(-13px) rotate(270deg);
    background-color: #ffce96;
    box-shadow: 0 0 10px #ffce96;
  }
  100% {
    transform: rotate(360deg);
    background-color: #3a9fc9;
    box-shadow: 0 0 10px #3a9fc9;
  }
}
@keyframes fly {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes fly-m {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes typing {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
@keyframes blink {
  100% {
    border-right-color: transparent;
  }
}
@keyframes popup {
  100% {
    right: 0;
  }
}
@keyframes popupoff {
  100% {
    transform: translateX(100%);
  }
}
@keyframes mp3 {
  0% {
    height: 10%;
  }
  50% {
    height: 50%;
  }
  100% {
    height: 10%;
  }
}
.main-header {
  font-size: 30px;
  font-weight: bold;
}

.page {
  font-family: "Rubik", "Arial", sans-serif;
  height: 100%;
