  line-height: 1.8;
}

.page__body {
  background-color: #0c0500;
  color: #ffffff;
  min-width: 320px;
  min-height: 100%;
  height: 3600px;
  margin: 0 auto;
}

.main-content {
  transform-style: preserve-3d;
  height: 100%;
}

.container {
  width: 100%;
  height: 100%;
  position: fixed;
  perspective: 1500px;
}

.gallery {
  transform-style: preserve-3d;
  height: 100%;
}

.frame {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.6s cubic-bezier(0.16, 1, 0.3, 1), opacity 0.7s ease;
  will-change: transform;
  transform-style: preserve-3d;
}
.frame--bg::before {
  content: "";
  position: absolute;
  z-index: -1;
  width: 170vw;
  height: 170vh;
  background-color: rgba(12, 5, 0, 0.9);
}
.frame--bg-blur {
  backdrop-filter: blur(4px);
}

.frame__title {
  font-weight: 300;
  text-align: center;
  font-size: calc((1vw + 1vh) * 2);
  line-height: 1.1;
  letter-spacing: 3px;
  margin-top: 0;
  margin-bottom: 0;
  border-right: 2px solid rgba(255, 255, 255, 0.7);
  animation: typing 4s steps(45) normal, blink 0.8s 10 forwards;
  white-space: nowrap;
  overflow: hidden;
}
@media (max-width: 767px) {
  .frame__title {
    letter-spacing: 2px;
    font-size: calc((1vw + 1vh) * 1.2);
  }
}

.frame__image {
  display: block;
  position: relative;
  background-color: #d38440;
  object-fit: cover;
  width: calc((1vw + 1vh) * 36);
  height: calc((1vw + 1vh) * 26);
}
.frame__image--right {
  left: calc(18 * (1vw + 1vh) + 30px);
}
@media (max-width: 767px) {
  .frame__image--right {
    left: calc(15.652173913 * (1vw + 1vh) + 30px);
  }
}
.frame__image--vertical {
  width: calc((1vw + 1vh) * 26);
  height: calc((1vw + 1vh) * 36);
  right: calc(13 * (1vw + 1vh) + 30px);
}
@media (max-width: 767px) {
  .frame__image--vertical {
    right: calc(11.3043478261 * (1vw + 1vh) + 30px);
  }
}

.frame__content--text-right {
