.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.form__container {
  opacity: 0;
  position: absolute;
  background-color: #0c0500;
  background-image: url("../img/im-5.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 0;
  top: 45%;
  transition: top 1s ease, height 1s ease, opacity 1.7s ease;
  z-index: 1;
}
@media (min-resolution: 2dppx) {
  .form__container {
    background-image: url("../img/im-5@2x.jpg");
  }
}
.form__container--show {
  height: 100%;
  opacity: 1;
  top: 0;
}
.form__container--show .form__content {
  transition: height 1s ease, opacity 1s ease;
  overflow: visible;
  opacity: 1;
}

.form__container::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  background-color: rgba(0, 0, 0, 0.7);
}

.form__content {
  position: absolute;
  left: 50%;
  transition: opacity 1s ease 0s, height 1s ease 0s;
  opacity: 0;
  height: 0;
  overflow: hidden;
  padding-top: 30px;
  max-width: 600px;
}
@media (min-width: 768px) {
  .form__content {
    padding-right: 10vw;
  }
}
@media (max-width: 767px) {
  .form__content {
    transform: translateX(-50%);
    padding-right: 10px;
    padding-left: 10px;
    padding-top: 20px;
  }
}
.form__content .form__title {
  font-weight: 500;
  font-size: calc((1vw + 1vh) * 1.1);
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 10px;
  letter-spacing: 1px;
}
.form__content .form__text {
  margin-top: 0;
  margin-bottom: 20px;
  line-height: 1.2;
  font-weight: 300;
  font-size: 1rem;
}
@media (max-width: 767px) {
  .form__content .form__text {
    margin-bottom: 15px;
  }
}

.feedback {
  position: relative;
}
.feedback::after {
  content: "";
  position: absolute;
  z-index: 2;
  inset: 0;
  background-color: rgba(12, 5, 0, 0.4);
  box-shadow: 0 0 10px #0c0500;
  display: none;
  transition: opacity 0.5s ease, visibility 0.5s ease;
}
.feedback::before {
  content: "";
  z-index: 3;
  position: absolute;
  left: 45%;
  top: 30%;
  width: 10px;
  height: 10px;
  background-color: #3a9fc9;
  border: 2px solid #ffffff;
  animation-name: loading;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-timing-function: ease-in;
  display: none;
}
.feedback--sending::after, .feedback--sending::before {
  display: block;
}

.feedback__list {
  padding: 0;
  list-style: none;
  display: grid;
  gap: 15px;
}
@media (min-width: 768px) {
  .feedback__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .feedback__list .feedback__item:nth-child(3),
.feedback__list .feedback__item:nth-child(4) {
    grid-column: span 2;
  }
}
@media (max-height: 450px) {
  .feedback__list {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    gap: 10px;
  }
  .feedback__list .feedback__item:nth-child(4) {
    grid-row: 1/-1;
    grid-column: 2/-2;
  }
  .feedback__list .feedback__item:nth-child(3) {
    grid-column: span 1;
  }
}

.feedback__btn {
  position: relative;
  background-color: transparent;
  border: 2px solid #ffce96;
  padding: 10px 20px;
  width: 100%;
  max-width: 180px;
  color: #ffce96;
  line-height: 1.1;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  display: block;
  margin-left: auto;
  cursor: pointer;
  outline: none;
