.popup {
  position: fixed;
  right: -220px;
  bottom: 15vh;
  width: 220px;
  z-index: 5;
  opacity: 0;
  visibility: hidden;
}
.popup--failed-show {
  opacity: 1;
  visibility: visible;
  animation: popup 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards, popupoff 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) 5s forwards;
}
.popup--success-show {
  opacity: 1;
  visibility: visible;
  animation: popup 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) forwards, popupoff 1s cubic-bezier(0.68, -0.6, 0.32, 1.6) 5s forwards;
}
