  line-height: 1.3;
  font-size: 0.5rem;
  font-weight: 300;
  margin: 0 0 0 0;
  padding: 10px 10px 10px 15px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .popup__text {
    font-size: 0.7rem;
    padding: 5px 10px 5px 10px;
  }
}

.main-header__content {
  position: fixed;
  z-index: 5;
  right: 20px;
  top: 20px;
}
@media (max-width: 767px) {
  .main-header__content {
    right: 10px;
    top: 10px;
  }
}

.main-header__button {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 1px solid rgba(12, 5, 0, 0.5);
  box-shadow: 2px 2px 9px 1px rgba(255, 255, 255, 0.3), -2px -2px 9px 1px rgba(255, 255, 255, 0.3);
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: box-shadow 0.4s ease;
  outline: none;
}
.main-header__button:hover, .main-header__button:focus {
  box-shadow: 2px 2px 9px 1px rgba(255, 255, 255, 0.7), -2px -2px 9px 1px rgba(255, 255, 255, 0.7);
}

.main-header__button-animation {
  display: block;
  width: 3px;
  height: 10%;
  background-color: #ffffff;
  border-radius: 3px;
  animation: mp3 1s linear infinite;
  transition: background-color 0.3s ease;
}
.main-header__button-animation:not(:last-child) {
  margin-right: 2px;
}
.main-header__button-animation:nth-child(2) {
  animation-delay: 0.2s;
}
.main-header__button-animation:nth-child(3) {
  animation-delay: 0.4s;
}
.main-header__button-animation:nth-child(4) {
  animation-delay: 0.6s;
}
.main-header__button-animation:nth-child(5) {
  animation-delay: 0.8s;
}

.main-header__button--pause .main-header__button-animation {
  background-color: rgba(255, 255, 255, 0.5);
}