@keyframes loading {
  0% {
    transform: translateX(0) translateY(0);
    background-color: $bg-blue;
    box-shadow: 0 0 10px $bg-blue;
  }

  25% {
    transform: translateX(13px) rotate(90deg);
    background-color: $invalid-color;
    box-shadow: 0 0 10px $invalid-color;
  }

  50% {
    transform: translateX(13px) translateY(-13px) rotate(180deg);
    background-color: $bg-dark-gray;
    box-shadow: 0 0 10px $bg-dark-gray;
  }

  75% {
    transform: translateX(0) translateY(-13px) rotate(270deg);
    background-color: $btn-decor;
    box-shadow: 0 0 10px $btn-decor;
  }

  100% {
    transform: rotate(360deg);
    background-color: $bg-blue;
    box-shadow: 0 0 10px $bg-blue;
  }
}

@keyframes fly {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(10px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes fly-m {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(5px);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes typing {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

@keyframes blink {
  100% {
    border-right-color: transparent;
  }
}

@keyframes popup {
  100% {
    right: 0;
  }
}

@keyframes popupoff {
  100% {
    transform: translateX(100%);
  }
}

@keyframes mp3 {
  0% {
    height: 10%;
  }

  50% {
    height: 50%;
  }

  100% {
    height: 10%;
  }
}
